.header {
  // border: 1px black solid;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 50px;
  box-shadow: 2px 2px 8px 3px rgba(0, 0, 0, 0.1);
  .header-title {
    //   border: 1px black solid;
    display: flex;
    justify-content: center;
    flex: 1;
    h1 {
      font-weight: bold;
    }
    p {
      font-size: 50px;
      font-weight: bold;
    }
  }

  .header-image {
    // border: 1px black solid;
    display: flex;
    justify-content: center;
    flex: 1;
    //   border: 1px black solid;
    img {
      border-radius: 200%;
      max-width: 50%;
      min-width: 200px;
      box-shadow: 2px 2px 8px 3px rgba(0, 0, 0, 0.25);
    }
  }
}

.body {
  display: flex;
  .body-box {
    margin-top: 10%;
    width: 100%;
    padding: 40px;
    box-shadow: 2px 2px 8px 3px rgba(0, 0, 0, 0.1);

    .box-header{
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      font-size: 50px;
      font-weight: bold;
      margin-bottom: 20px;
      .icon{
        margin: 20px;
        font-size: 30px;
      }
    }

    .box-body{
      .box-title-skill{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        div{
          width: 50%;
          padding: 50px;
          box-shadow: 2px 2px 8px 3px rgba(0, 0, 0, 0.1);

        }
      }
    }
  }
}

@media only all and (max-width: 800px) {
  /* For tablets: */
  .header {
    text-align: center;
    align-items: center;
    justify-content: center;
    .header-title {
      p {
        font-size: 30px;
      }
    }

    .header-image {
      margin-top: 10px;
      img {
        min-width: 200px;
      }
    }
  }
}
