.App {
}
.hide{
  display: none;
}

@media all and (min-width: 801px) {
  .nav-app {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;

    .header-title {
      font-size: 40px;
      
   
      
      .hamburger-menu {
        display: none;
      }
      a {
        text-decoration: none;
        color: black;
      }
      font-weight: bold;
    }
    .header-list {
      display: flex;
      align-items: center;

      li {
        list-style: none;
        margin-left: 20px;

        .link {
          color: black;
          text-decoration: none;
          transition: 0.2s ease-in-out;
          padding: 10px;
        }

        .link:hover {
          color: #0649f0;
          transition: 0.2s ease-in-out;
        }
      }
    }
  }
}

@media all and (max-width: 800px) {
  .nav-app {
    position: sticky;
    top: 0;
    display: block;
    justify-content: center;
    align-items: center;

    padding-top: 10px;
    .header-title {
      display: flex;
      justify-content: space-between;
      font-size: 40px;
      
      .hamburger-menu {
        margin-right: 10px;
        cursor: pointer;
      }
      a {
        text-decoration: none;
        color: black;
      }
      font-weight: bold;
    }
    .header-list {
      display: block;
      text-align: center;
      align-items: center;

      li {
        padding: 10px;
        border: 1px black solid;
        list-style: none;
        margin-left: 20px;

        .link {
          color: black;
          text-decoration: none;
          transition: 0.2s ease-in-out;
          padding: 10px;
        }

        .link:hover {
          color: #0649f0;
          transition: 0.2s ease-in-out;
        }
      }
    }
  }
}
